import React from 'react';
import {AvForm, AvField} from 'availity-reactstrap-validation'
import {Link} from "react-router-dom";
import {LoginUser} from "../redux/action/authAction";
import {connect} from "react-redux";
import {changePasswordForSend} from "../redux/action/createAgentAction";
const ForgotPassword = (props) => {




    const forgotPassword = (event , value) => {
      props.changePasswordForSend(value)
    }

    return (
        <div className='login-page'>
            <div className="login-form-group">

                <h3>
                    Log in
                </h3>

                <AvForm onValidSubmit={forgotPassword}>
                    <AvField

                        name='email'
                        required
                        placeholder="Email"
                        type="text"
                    />

                    <div type="submit" className="create-button-group">


                        <button   className="change-password-btn btn ml-3">
                            Change password
                        </button>

                    </div>
                </AvForm>



            </div>
        </div>
    );
};

const mapStateToProps = (state) => {
    return {
        isLoading: state.login.isLoading
    }
}

export default connect(mapStateToProps, {LoginUser, changePasswordForSend})(ForgotPassword);